import { useDispatch, useSelector } from "react-redux";
import ButtonBase from "../ButtonBase";
import { fetchExampleTasks, selectFetchExampleTasksState } from "../../tasksSlice";


const SingleButton = () => {
    const fetchExampleTasksState = useSelector(selectFetchExampleTasksState);
    const disabled = fetchExampleTasksState === "success" || fetchExampleTasksState === "error"

    const dispatch = useDispatch()

    return (
        <ButtonBase
            onClick={() => dispatch(fetchExampleTasks())}
            disabled={disabled}
        >
            {!fetchExampleTasksState && "Pobierz przykadowe zadania"}
            {fetchExampleTasksState === "loading" && "Pobieranie..."}
            {fetchExampleTasksState === "success" && "Pobrano przykadowe zadania "}
            {fetchExampleTasksState === "done" && "Odwie przykadowe zadania"}
            {fetchExampleTasksState === "error" && "Przykadowe zadania niedostpne"}
        </ButtonBase>
    )
};

export default SingleButton