import { Container } from "../../../common/Container";
import Header from "../../../common/Header";
import Section from "../../../common/Section";
import { SectionContainer } from "../../../common/SectionContainer";

function AuthorPage() {

    return (
        <Container>
            <Header title={"O autorze"} />
            <Section
                title={"Pawe Kruczek"}
                body={
                    <SectionContainer>
                        <p>Cze! Studiuje <strong>informatyk</strong>, na codzie pracuj jako deweloper w firmie zajmujcej si <strong>automatyzacjami procesw biznesowych</strong> i budowaniem aplikacji mobilnych i webowych</p>
                        <p>W wolnym czasie lubi spdza czas aktywnie, i na basen, pj w gry, czy te pooglda jaki film lub pogra na konsoli. Najczciej gram w <strong>Dead by daylight</strong> lub jakie gry fabularne jednoosobowe.</p>
                    </SectionContainer>
                }
            />
        </Container>
    );
};

export default AuthorPage;